import React from 'react'
import {
  FormGroup,
  Input,
  ModalBody,
  ModalFooter,
  Button,
  Modal,
  CardBody,
  Card
} from 'reactstrap'
import { Menu, Search, Bookmark, Star, Trash } from 'react-feather'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { connect } from 'react-redux'
import {
  getTodos,
  completeTask,
  starTask,
  importantTask,
  trashTask,
  searchTask
} from '../../redux/actions/diary/index'
class DiaryList extends React.Component {
  static getDerivedStateFromProps(props, state) {
    if (props.app.todo.routeParam !== state.currentLocation) {
      return {
        todos: props.app.todo.todos
      }
    }
    return null
  }
  state = {
    open: false,
    id: null,
    todos: [],
    handleUpdateTask: null,
    currentLocation: this.props?.routerProps?.location?.pathname,
    value: ''
  }
  getdiaryLIst = async () => {
    if (!this.state.currentLocation) return
    await this.props.getTodos(this.state.currentLocation)
    this.setState({
      todos: this.props?.app?.todo?.todos,
      handleUpdateTask: this.props.handleUpdateTask
    })
  }

  UNSAFE_componentWillReceiveProps(props) {
    if (this.props.prevState) {
      this.setState({ todos: props.app.todo.todos })
      this.props.clearPrevState()
    }
  }
  componentDidMount() {
    this.getdiaryLIst()
  }

  handleOnChange = (e) => {
    this.setState({ value: e.target.value })
    this.props.searchTask(e.target.value)
  }

  render() {
    const { todos, handleUpdateTask, value } = this.state
    let routerFilter = this.props.routerProps.match.params.filter
    let todosArr = value.length ? this.props.app.todo.filteredTodos : todos

    if (todosArr === undefined) {
      return (
        <div className="fallback-spinner vh-100">
          <div className="loading">
            <div className="effect-1 effects"></div>
            <div className="effect-2 effects"></div>
            <div className="effect-3 effects"></div>
          </div>
        </div>
      )
    }
    console.log(todosArr)
    let renderTodos =
      todosArr.length > 0 ? (
        todosArr.map((todo, i) => {
          var options = {
            weekday: 'long',
            year: 'numeric',
            month: 'long',
            day: 'numeric'
          }
          var today = new Date(todo.createdAt)
          return (
            <li
              style={{ border: 'none', padding: '0', margin: '0 1.6rem' }}
              className={`todo-item ${todo.isCompleted ? 'completed' : ''}`}
              key={i}
              onClick={() => {
                handleUpdateTask(todo)
              }}
            >
              <Modal
                isOpen={this.state.open}
                className="modal-dialog-centered"
                backdrop="static"
              >
                <ModalBody>
                  Are you sure you want to delete this record?
                </ModalBody>
                <ModalFooter>
                  <Button
                    color="primary"
                    className="button-label"
                    onClick={() => {
                      this.setState({ open: false })
                    }}
                  >
                    Cancel
                  </Button>
                  <Button
                    color="danger"
                    onClick={() => {
                      this.setState({ open: false })
                      this.props.trashTask(this.state.id)
                    }}
                    className="button-label"
                  >
                    Delete
                  </Button>
                </ModalFooter>
              </Modal>
              <Card className="mt-1 mb-0" style={{ minHeight: '110px' }}>
                <CardBody className="p-0 d-flex">
                  <div id="left-content" className="w-100 p-1">
                    <div id="top-content" className="d-flex">
                      <p style={{ fontSize: '1rem' }}>
                        {today.toLocaleDateString('en-US', options)}&nbsp;
                        {'-'}
                      </p>
                      <p style={{ fontSize: '1.15rem', fontWeight: 'bold' }}>
                        &nbsp;
                        {todo.title}
                      </p>
                    </div>
                    <div id="bottom-content">
                      {todo.desc.length > 0 ? (
                        <p
                          className="todo-desc mb-0"
                          style={{
                            wordBreak: 'break-all',
                            whiteSpace: 'normal',
                            fontSize: '1.15rem'
                          }}
                        >
                          {todo.desc}
                        </p>
                      ) : (
                        ''
                      )}
                    </div>
                  </div>
                  <div id="options">
                    <div
                      style={{ maxHeight: '110px' }}
                      className="d-flex flex-column justify-content-between"
                    >
                      <div
                        style={{
                          padding: '0.5rem',
                          backgroundColor: todo.isImportant
                            ? 'var(--danger)'
                            : '',
                          borderTopRightRadius: '0.5rem'
                        }}
                        onClick={(e) => {
                          e.stopPropagation()
                          this.props.importantTask(todo)
                        }}
                      >
                        <Star
                          size={23}
                          color={`${
                            todo.isImportant ? 'white' : 'currentColor'
                          }`}
                          // className={`${todo.isImportant ? 'text-danger' : ''}`}
                        />
                      </div>
                      <div
                        style={{
                          padding: '0.5rem',
                          backgroundColor: todo.isStarred
                            ? 'var(--warning)'
                            : ''
                        }}
                        onClick={(e) => {
                          e.stopPropagation()
                          this.props.starTask(todo)
                        }}
                      >
                        <Bookmark
                          size={23}
                          color={`${todo.isStarred ? 'white' : 'currentColor'}`}
                        />
                      </div>
                      {routerFilter !== 'trashed' ? (
                        <div
                          style={{ padding: '0.5rem' }}
                          // onClick={(e) => {
                          //   //e.stopPropagation()
                          //   this.openModal(todo.id)
                          // }}
                        >
                          <Trash
                            size={23}
                            onClick={(e) => {
                              e.stopPropagation()
                              this.setState({ id: todo.id, open: true })
                            }}
                          />
                        </div>
                      ) : null}
                    </div>
                  </div>
                  {/* <div className="todo-title-wrapper d-flex justify-content-between mb-50">
                    <div className="todo-title-area d-flex align-items-center">
                      <div className="title-wrapper d-flex"></div>

                      {todo.tags.length > 0 ? (
                        <div className="chip-wrapper">
                          {todo.tags.map((tag, i) => (
                            <div className="chip mb-0" key={i}>
                              <div className="chip-body">
                                <span className="chip-text">
                                  <span
                                    className={`bullet bullet-${
                                      tag === 'backend'
                                        ? 'warning'
                                        : tag === 'doc'
                                        ? 'success'
                                        : tag === 'bug'
                                        ? 'danger'
                                        : 'primary'
                                    } bullet-xs`}
                                  />
                                  <span className="text-capitalize ml-25">
                                    {tag}
                                  </span>
                                </span>
                              </div>
                            </div>
                          ))}
                        </div>
                      ) : null}
                    </div>

                    <div
                      className={`todo-item-action d-flex ${
                        routerFilter === 'trashed' ? 'justify-content-end' : ''
                      }`}
                    >
                      <div
                        className={`todo-item-info d-inline-block ${
                          routerFilter === 'trashed' ? 'mr-1' : 'mr-1 mr-sm-0'
                        }`}
                        onClick={(e) => {
                          e.stopPropagation()
                          this.props.importantTask(todo)
                        }}
                      >
                        <Star
                          size={17}
                          className={`${todo.isImportant ? 'text-danger' : ''}`}
                        />
                      </div>
                      <div
                        className="todo-item-favorite d-inline-block mr-1 mr-sm-0"
                        onClick={(e) => {
                          e.stopPropagation()
                          this.props.starTask(todo)
                        }}
                      >
                        <Bookmark
                          size={17}
                          className={`${todo.isStarred ? 'text-warning' : ''}`}
                        />
                      </div>
                      {routerFilter !== 'trashed' ? (
                        <div
                          className="todo-item-delete d-inline-block mr-1 mr-sm-0"
                          // onClick={(e) => {
                          //   //e.stopPropagation()
                          //   this.openModal(todo.id)
                          // }}
                        >
                          <Trash
                            size={17}
                            onClick={(e) => {
                              e.stopPropagation()
                              this.setState({ id: todo.id, open: true })
                            }}
                          />
                        </div>
                      ) : null}
                    </div>
                  </div>
                  {todo.desc.length > 0 ? (
                    <p
                      className="todo-desc mb-0"
                      style={{ wordBreak: 'break-all', whiteSpace: 'normal' }}
                    >
                      {todo.desc}
                    </p>
                  ) : (
                    ''
                  )} */}
                </CardBody>
              </Card>
            </li>
          )
        })
      ) : (
        <p className="p-1 text-center mt-2 font-medium-3 text-bold-500">
          No tasks at this time
        </p>
      )

    return (
      <div className="content-right h-100">
        <div className="todo-app-area h-100">
          <div className="todo-app-list-wrapper h-100">
            <div className="todo-app-list h-100">
              <div className="app-fixed-search">
                <div
                  className="sidebar-toggle d-inline-block d-lg-none"
                  onClick={() => this.props.mainSidebar(true)}
                >
                  <Menu size={24} />
                </div>
                <FormGroup
                  style={{
                    display: 'flex',
                    margin: 'auto',
                    justifyContent: 'center'
                  }}
                  className="position-relative has-icon-left"
                >
                  <div style={{ borderRadius: '1.428rem' }}>
                    <div className="form-control-position">
                      <Search size={15} />
                    </div>
                    <Input
                      style={{
                        width: '100%'
                      }}
                      type="text"
                      placeholder="Search..."
                      onChange={(e) => this.handleOnChange(e)}
                      value={value}
                    />
                  </div>
                </FormGroup>
              </div>
              <PerfectScrollbar
                className="todo-task-list"
                options={{
                  wheelPropagation: false
                }}
              >
                <ul className="todo-task-list-wrapper">{renderTodos}</ul>
              </PerfectScrollbar>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
const mapStateToProps = (state) => {
  return {
    app: state.todoApp
  }
}
export default connect(mapStateToProps, {
  getTodos,
  completeTask,
  starTask,
  importantTask,
  trashTask,
  searchTask
})(DiaryList)
